import { Component, OnInit, Output, ViewEncapsulation, ViewChild, ChangeDetectorRef, EventEmitter } from '@angular/core';
import { ActivatedRoute, Router, NavigationEnd } from '@angular/router';

import { MatDrawer } from '@angular/material/sidenav';

import { SharedService } from 'src/app/shared/services/shared.service';

@Component({
  selector: 'search-drawer',
  templateUrl: './search-drawer.component.html',
  styleUrls: ['./search-drawer.component.scss'],
  encapsulation  : ViewEncapsulation.None
})
export class SearchDrawerComponent implements OnInit {

  @ViewChild('matDrawer', {static: true})
  matDrawer: MatDrawer;

  drawerMode: 'over';

  isOpened:boolean = false;

  searchInput:string = "";

  @Output() public outputParams = new EventEmitter();

  constructor(
    private _changeDetectorRef: ChangeDetectorRef,
    private _activatedRoute: ActivatedRoute,
    private _router: Router,
    private sharedService:SharedService
  ) { }

  showSearchDrawer() {
    this.isOpened = !this.isOpened
  }

  closeDrawer(){
    this.isOpened = false;
  }

  submitSearchForm(form) {
    this.outputParams.emit(this.searchInput)
  }

  ngOnInit(): void {
  }

  ngOnChanges() {
    this.searchInput = "";
  }

}
